/* CSS Variables */
:root {
  --primary: #ddd;
  --dark: #333;
  --light: #fff;
  --shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
  --dark1: #5d5c61;
  --grey1: #637b7c;
  --lighterblue: #7395ae;
  --darkerblue: #557a95;
  --secondary: #b1a296;
}



.App {
  text-align: center;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  color: black;
  margin: 30px;
}



body {
  background: var(--grey1);
  margin: 30px 50px;
  line-height: 1.4;
}


/* ##################  Header ####################### */ 
/* ################################################## */
/* ######### HEADER ################# HEADER ######## */

.site-header {
  /* background-color: var(--dark); */
  background-color: var(--dark);
  display: grid;
  grid-template-columns: 0.7fr 0.7fr 1fr 1fr 1fr;
  
}


.site-header h1 {
  font-size: 5em;
  color: lightgray;
  grid-column-start: 1;
  grid-column-end: 3;
  margin: 0px;
}

.site-header .vertical-divider {
  width: 3px;
  height: 70%;
  align-self: center;
  background-color: black;
}


.site-header .header-strip {
  grid-row-start: 2;
  grid-column-start: 1;
  grid-column-end: 6;
  background-color: rgb(218, 210, 210);
}

.site-header .sub1, .sub2 {
  color: black;
  grid-row-start: 2;
  grid-row-end: 2;
  margin: 0px;
  padding: .6em;
}

.site-header .sub1 {
  grid-column-start: 1;
  grid-column-end: 1;
}
.site-header .sub2 {
  grid-column-start: 2;
  grid-column-end: 2;
}


/* ##################  Navigation ########################### */ 
/* ########################################################## */
/* ######### Navigation ################# Navigation ######## */


.main-nav ul {
  display: grid;
  grid-gap: 20px;
  padding: 0;
  list-style: none;
  grid-template-columns: repeat(5, 1fr);
}

.main-nav a {
  background: var(--primary);
  display: block;
  text-decoration: none;
  padding: 0.8rem;
  text-align: center;
  color: var(--dark);
  text-transform: uppercase;
  font-size: 1.1rem;
  box-shadow: var(--shadow);
}

.main-nav a:hover {
  background: var(--dark);
  color: var(--light);
}




/* ################## Main Content ############################## */ 
/* ############################################################## */
/* ######### Main Content ################# Main Content ######## */



.main-content {
  display: grid;
  background-color:var(--primary);
  grid-template-columns: 1fr 12.5fr 2.5fr;
  grid-template-rows: 4fr 1fr;
  height: 35em;
  border: 1px solid black;
}

.info-panel-container {
  grid-row: 1 / 2;
  grid-column: 3 / 4;
  background-color: var(--dark);
  display: grid;
  overflow-y: scroll;
}


.notes-container {
  grid-row: 2 / 3;
  grid-column: 1 / 4;
  color: var(--primary);
  background-color: var(--dark);
  border: 1px solid black;
}

.notes-container p {
  margin: auto;
  margin-top: 1em;
  width: 20em;
  height: 5em;
  color: black;
  background-color: whitesmoke;
}


.main-content-nav {
  display: grid;
  align-items: center;
}

.main-content-nav ul {
  display: grid;
  gap: 20px;
  padding: 0;
  list-style: none;
  grid-template-rows: repeat(auto, 1fr);
}


.main-content-nav a {
  background: var(--lighterblue);
  display: block;
  text-decoration: none;
  padding: 0.8rem;
  text-align: center;
  color: var(--dark);
  text-transform: uppercase;
  font-size: 1.1rem;
  box-shadow: var(--shadow);
}

.main-content-nav a:hover {
  background: var(--dark);
  color: var(--light);
}

.active {
  background-color: indianred;
  color: white;
}





