/* CSS Variables */
:root {
  --primary: #ddd;
  --dark: #333;
  --light: #fff;
  --shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
}

.content-container{
  grid-column-start: 1;
  grid-column-end: 3;
  overflow-y: scroll;
  /* justify-content: center;
  align-items: center; */

}

.pov-content-container h1 {
  margin-top: 0px;
  color: lightgray;
  /* justify-content: center;
  align-items: center; */
}

.pov-header {
  background-color: var(--dark);
}

.pov-content-container li {
  /* text-align:inherit;
  margin-left: auto;
  margin-right: auto; */
  margin: 1em;
  font-size:x-large;
}

.section {
  text-align: center;
  width: 60%;
  margin: auto;
  margin-bottom: 0em;
  padding: 0.5em;
